.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.font-main {
  font-weight: 75;
  font-size: 40px;
}

.font-smaller {
  font-weight: 75;
  font-size: 25px;
}

.citrus-navy {
  color: #00137F;
}

.citrus-blue {
  color: #77B0FF;
}

.icon {
  height: 20px;
  width: 20px;
}

.icon-large {
  height: 65px;
  width: 65px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
